<template>
    <div id="app">
        <nav class="navbar" role="navigation" aria-label="main navigation">
            <div class="navbar-brand">
                <a class="navbar-item">
                    <h1 v-if="store">{{store.header_title}}</h1>
                    <h1 v-else>{{load_text}}</h1>
                </a>
            </div>
            <div class="navbar-menu">
                <div class="navbar-end">
                    <div class="navbar-item">
                        <div v-if="store" class="buttons">
                            <button @click="saveSettings" type="button" class="button is-primary">{{store.button_save_text}}</button>
                            <a :href="store.back_page_modules" class="button is-light">{{store.back_page_modules_text}}</a>
                        </div>
                        <div v-else>{{load_text}}</div>
                    </div>
                </div>
            </div>
        </nav>

        <div v-if="store" class="container is-fluid">
            <br>
            <nav class="breadcrumb" aria-label="breadcrumbs">
                <ul>
                    <li v-for="(breadcrumb, index) in store.breadcrumbs" :key="index">
                        <a :href="breadcrumb.href">{{breadcrumb.title}}</a>
                    </li>
                </ul>
            </nav>
            <div class="columns">
                <div class="column is-two-thirds">
                    <div class="box">
                        <div class="tabs">
                            <ul>
                                <li
                                        v-for="(tab, routeName) in store.tabsLink"
                                        :key="routeName"
                                        :class="isTabActive(routeName)"
                                >
                                    <a @click.prevent="setTabActive(routeName)">{{tab}}</a>
                                </li>
                            </ul>
                        </div>
                        <div v-for="(tab, routeName) in store.tabsData" :key="routeName">
                            <TabPage
                                    :class="isTabActive(routeName) ? isTabActive(routeName) : 'is-hidden'"
                                    v-bind:routeName="routeName"
                                    v-bind:tabPage="tab"
                                    v-bind:isTabActive="isTabActive(routeName) ? true: false"
                            />
                        </div>
                    </div>
                </div>

                <div class="column">
                    <div class="box">
                        <div class="field">
                            <label class="label">{{store.apikey_text}}</label>
                            <div class="control">
                                <input class="input" type="text" name="dadatapro_api_key" :placeholder="store.apikey_text" v-model=store.apikey>
                            </div>
                        </div>

                        <div class="notification is-warning is-light" v-html="store.apikey_notification_text"></div>

                        <label class="label">{{store.status_text}}</label>

                        <div class="switch-field">
                            <input
                                    type="radio" id="module-on" name="dadatapro_status"
                                    v-model=store.status value="1" :checked="store.status"/>
                            <label for="module-on">Вкл</label> <input
                                type="radio" id="module-off" name="dadatapro_status"
                                v-model=store.status value="0" :checked="!store.status"/>
                            <label for="module-off">Выкл</label>
                        </div>
                    </div>

                    <div class="box">
                        <div class="notification is-warning is-light">Посмотреть ключи для группы Ф.И.О:
                            <a href="https://dadata.ru/suggestions/usage/name/#response" target="_blank">Подсказки: ФИО</a>
                        </div>
                        <div class="notification is-warning is-light">Посмотреть ключи для группы Email:
                            <a href="https://dadata.ru/suggestions/usage/email/#response" target="_blank">Подсказки: email</a>
                        </div>
                        <div class="notification is-warning is-light">Посмотреть ключи для группы адресс:
                            <a href="https://dadata.ru/suggestions/usage/address/#response" target="_blank">Подсказки: адреса</a>
                        </div>
                        <div class="notification is-warning is-light">Посмотреть ключи для группы организации:
                            <a href="https://dadata.ru/suggestions/usage/party/#response" target="_blank">Подсказки: организации </a>
                        </div>
                        <div class="notification is-warning is-light">Посмотреть ключи для группы адресс:
                            <a href="https://dadata.ru/suggestions/usage/address/#response" target="_blank">Подсказки: адреса</a>
                        </div>
                    </div>

                    <div class="box">
                        <p class="is-size-7" v-html="store.cooperate"></p>
                    </div>
                </div>
            </div>
        </div>
        <div v-else class="container">
            <br>
            <div class="box">
                <h1>{{load_data_text}}</h1>
            </div>
        </div>
    </div>
</template>

<script>
  import TabPage from './components/TabPage.vue';

  const axios = require('axios').default;
  //import requestData from './../config/request.js';

  export default {
    name: 'App',
    components: {
      TabPage
    },
    data: () => {
      return {
        store: null,
        load_text: 'Loading...',
        load_data_text: 'Loading data...'
      };
    },
    methods: {
      isTabActive(tabActive) {
        return (this.store.activeItem === tabActive ? 'is-active' : '');
      },
      setTabActive(tabActive) {
        this.store.activeItem = tabActive;
      },
      saveSettings() {
        axios({
          url: this.$root.settings.link_save + `&token=${this.$root.settings.token}&timestamp=${new Date().getTime()}`,
          method: 'post',
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded',
            'X-Requested-With': 'XMLHttpRequest'
          },
          data: 'store=' + JSON.stringify(this.store)
        }).then(response => {
          console.log(response);

        }).catch(error => {
          console.log(error);
        });
      }
    },
    mounted() {
      // this.store = requestData;
      // console.log(this.store);
      axios.get(this.$root.settings.link_settings + `&token=${this.$root.settings.token}&timestamp=${new Date().getTime()}`).then(response => {
        console.log(response);
        this.store = response.data;
        console.log(this.store);
      }).catch(error => {
        console.log(error);
      });
    }
  };
</script>

